import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import rightBox from '@/components/common-components/right-box.vue';
import editBox from '@/components/common-components/edit-box.vue';
import editForm from './edit-food.vue';
import addNav from './add-nav.vue';
import { deepClone } from '@/until';
let businessManagement = class businessManagement extends Vue {
    constructor() {
        super(...arguments);
        this.showEditbox = false;
        this.showAddNav = false;
        this.pageTitle = '商品管理';
        this.data = [];
        this.currentIndex = 0;
        this.modelIndex = 0;
    }
    get shopId() {
        return this.$route.params.id;
    }
    get shopName() {
        return this.$route.query.shopName;
    }
    created() {
        let shopData = deepClone(this.$route.params.shopData);
        this.$set(this, 'data', shopData);
    }
    navClick(index) {
        this.currentIndex = index;
        this.$refs.rightBox.scrollTop = 0;
    }
    editMenu(menu, index) {
        this.modelIndex = index;
        console.log(menu, 'ooooooooooooooo');
        let cloneMenu = deepClone(menu);
        this.showEditbox = true;
        this.$nextTick(() => {
            this.$refs.editForm.setData(cloneMenu);
        });
    }
    changeSuccess(model) {
        this.$set(this.data[this.currentIndex].menu, this.modelIndex, model);
        this.showEditbox = false;
    }
    navChangeSuccess(model) {
        let currentModel = deepClone(model);
        if (!currentModel.id) {
            let addObj = {
                id: 'C' + (this.data.length ? this.data.length : 0),
                name: currentModel.name,
                menu: []
            };
            this.data.push(addObj);
        }
        this.showAddNav = false;
    }
    addNav() {
        this.showAddNav = true;
    }
    addFood() {
        let menu = this.data[this.currentIndex].menu || [];
        let id = this.data[this.currentIndex].id + '-' + menu.length;
        this.editMenu({ id: id, name: '', price: 0, num: 0 }, menu.length);
    }
    saveFood() {
        this.$store.dispatch('updateShopInfo', {
            shopId: this.shopId,
            type: 'shopData',
            value: this.data
        }).then((e) => {
            console.log(e, 'djflakjflajflaj');
            if (e.success) {
                this.$createDialog({
                    type: 'alert',
                    content: '保存成功',
                    onConfirm: (e) => {
                        this.$router.push({
                            name: 'businessManagement',
                            params: {
                                id: this.shopId,
                                oprate: 'update',
                                type: 'shopData',
                                data: this.data
                            }
                        });
                    }
                }).show();
            }
        });
    }
};
businessManagement = __decorate([
    Component({
        components: {
            page,
            rightBox,
            editBox,
            editForm,
            addNav
        }
    })
], businessManagement);
export default businessManagement;
